/* Contenedor de la tarjeta grande */
.large-card {
    background-color: #fff;
    border: 1px solid #ddd;
    border-radius: 16px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    width: 80%; /* Ajusta según sea necesario */
    max-width: 600px; /* Ajusta según sea necesario */
    margin: 20px auto;
    padding: 20px;
    text-align: center;
}

/* Título de la tarjeta */
.large-card h2 {
    font-size: 24px;
    font-weight: bold;
    color: #333;
    margin-bottom: 16px;
}

/* Párrafo de la tarjeta */
.large-card p {
    font-size: 18px;
    color: #555;
    margin-bottom: 16px;
}

/* Imagen de la tarjeta */
.large-card img {
    width: 100%;
    height: auto;
    border-radius: 8px;
}
